(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ngRoute', 'toastr']);

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creation" is avaible by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
      .module('asylumjourneyFrontend')
      .service('services', services);

  /** @ngInject */
  function services() {
    var data = [
      {
        'name': 'Schools referral',
        'url': 'https://groups.google.com/forum/#!forum/sheffugees',
        'description': 'Description'
      }
    ];

    this.getServices = getServices;

    function getServices() {
      return data;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, services) {
    var vm = this;

    vm.services = [];

    activate();

    ////////

    function activate() {
      getServices();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function getServices() {
      vm.services = services.getServices();
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('asylumjourneyFrontend')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;
  }

})();

angular.module("asylumjourneyFrontend").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div class=\"col\" ng-repeat=\"service in main.services | orderBy:\'rank\'\"><div class=\"thumbnail\"><div class=\"caption\"><h3>{{ service.name }}</h3><p>{{ service.description }}</p><p><a ng-href=\"{{ service.url }}\">{{ service.url }}</a></p></div></div></div></div>");
$templateCache.put("app/components/navbar/navbar.html","<nav class=\"navbar\"><a href=\"https://github.com/Swiip/generator-gulp-angular\">Gulp Angular</a><ul><li class=\"active\"><a ng-href=\"#\">Home</a></li><li><a ng-href=\"#\">About</a></li><li><a ng-href=\"#\">Contact</a></li></ul><ul class=\"acme-navbar-text\"><li>Application was created {{ vm.relativeDate }}.</li></ul></nav>");}]);